@import url('./reset.css');

body {
  margin: 20px 0 0 0;
  padding: 0;
  font-family: sans-serif;
  background: #949;
}

.instrument {
  background: #eee;
  width: 400px;
  margin: 0 auto;
  padding: 20px;
  border-radius: 10px;
}

.monome {
  display: block;
  width: 360px;
  height: 395px;
  margin: 0 auto;
  background: #555;
  padding: 0 5px;
}

.tile {
  font-size: 0px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  background: #ddd;
  display: inline-block;
  width: 10%;
  height: 10%;
  margin: 1.75% 1.25% 0 1.25%;
}

.enabled {
  background: #a3a;
}

.active {
  background: yellow;
}

.active.enabled {
  background: #c5c;
}

input[type="button"] {
  border: none;
  background: #727;
  color: white;
  font-size: 20px;
  padding: 5px 10px;
  border-radius: 5px;
  margin: 10px;
  display: block;
}

input[type="button"]:hover {
  background: #359;
}

.controls {
  background: white;
  margin-top: 20px;
  border: 2px solid #555;
  border-radius: 5px;
  display: inline-block;
}

.controls .knob {
  margin: 10px 5px;
  display: inline-block;
  border: 1px solid gray;
  border-radius: 5px;
  padding: 5px;
}
.knob h3 {
  margin-bottom: 10px;
}



